.logo-block__item {
  max-width: 220px;
  width: 100%;
  height: 48px;
  /* background-image: url(../../../images/partners/Gazprom.svg); */
  background-position: center;
  background-repeat: no-repeat;
  background-size: 112px 48px;
  padding: 0;
  margin: 0;
}

.logo-block__item:first-child {
  background-position:left;
}

.logo-block__item:last-child {
  background-position:right;
}

.logo-block__item:nth-child(6) {
  background-position:left;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .logo-block__item {
    max-width: 48px;
  }
}